import Fade from 'react-reveal/Fade'
import { contact } from '../../portfolio'
import './Contact.css'

const Contact = () => {
  if (!contact.email) return null
  return (
    <Fade>
      <section className='section contact center' id='contact'>
        <h2 className='section__title'>Contact</h2>
        <div className='cont-btn'>
          <a
            className='btn-contact outline hvr-buzz-out fa-mail mr5 noSelect'
            href={`mailto:${contact.email}`}
          >
            <i className='mr2 fas fa-envelope' />
            Mail
          </a>
          <a
            className='btn-contact hvr-buzz-out outline mr5 noSelect'
            href={`${contact.github}`}
            target='_blank'
            rel='noreferrer'
          >
            <i className='mr2 fab fa-github' />
            GitHub
          </a>
          <a
            className='btn-contact hvr-buzz-out outline mr5 noSelect'
            href={`${contact.linkedin}`}
            target='_blank'
            rel='noreferrer'
          >
            <i className='mr2 fab fa-linkedin' />
            Linkedin
          </a>
        </div>
      </section>
    </Fade>
  )
}

export default Contact
